import Jreact from './lib/jreact' 
import JreactDOM from './lib/jreact-dom'

let num = 0
let timer = null
let styleObj = {
  color: 'red',
  fontSize: '20px'
}

onStart() //‰∏ÄÂºÄÂßãÊó∂ÊâßË°å

function onStart() {
  console.log('click me')
  timer = setInterval(() => { //ÂêØÂä®Êó∂ÔºåÊØèÁßíÈíüËÆ°Êó∂‰∏ÄÊ¨°ÔºåÂÅö‰∏ÄÊ¨°Ê∏≤Êüì
    num++
    JreactDOM.render((
      <div className="wrapper">
        <h1 style = { styleObj }>Number: { num }</h1>
        <button onClick = { onStart }>start</button>
        <button onClick = { onPause }>pause</button>
      </div>
    ), document.querySelector('#app'))
  }, 1000)
}

function onPause() {
  clearInterval(timer) //ÁÇπÂáªÂÅúÊ≠¢Êó∂ÔºåÊ∏ÖÈô§ÂÆöÊó∂Âô®
}