function render(vnode, container) { //ÊØèÊ¨°Ë∞ÉÁî® render Êó∂ÔºåÂÖàÊää‰πãÂâçÁöÑÊ∏ÖÁ©∫
  container.innerHTML = ''
  _render(vnode, container)
}

function _render(vnode, container) {
  if (typeof vnode === 'string' || typeof vnode === 'number') { //Â¶ÇÊûúÊòØ string ÊàñËÄÖ nubmer ÈÉΩÂéªÂàõÂª∫ÊñáÊú¨ËäÇÁÇπ
    return container.appendChild(document.createTextNode(vnode))
  }

  if (typeof vnode === 'object') {
    let dom = document.createElement(vnode.tag)
    setAttribute(dom, vnode.attrs)
    if (vnode.children && Array.isArray(vnode.children)) {
      vnode.children.forEach(vnodeChild => {
        _render(vnodeChild, dom) //ËÆ∞ÂæóËøôÈáåÊòØ _render , ËøôÈáåÁöÑÈÄªËæëÊòØ‰∏çÊ∏ÖÁ©∫ÁöÑ
      })
    }

    container.appendChild(dom)
  }
}

function setAttribute(dom, attrs) {
  for (let key in attrs) {
    if (/^on/.test(key)) { //ÂØπ‰∫ã‰ª∂ÁªëÂÆöÁöÑÂ§ÑÁêÜÔºå‰ª• on ÂºÄÂ§¥ÁöÑÔºådom[onclick] = attrs[onClick]
      dom[key.toLocaleLowerCase()] = attrs[key]
    } else if (key === 'style') { //ÂØπ style ÁöÑÂ§ÑÁêÜ
      Object.assign(dom.style, attrs[key]) //Êñ∞Â¢ûÁöÑ‰ºöËµãÂÄºÂà∞ dom.style ‰∏äÔºåÂêåÂêçÁöÑÂ±ûÊÄß‰ºöË¶ÜÁõñ
    } else { //ÂÖ∂‰ªñÁöÑÁõ¥Êé•‰Ωú‰∏∫ dom ÁöÑÂ±ûÊÄß
      dom[key] = attrs[key]
    }
  }
}

export default {
  render
}